.home-container{
    background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    padding: 100px 10%;
    min-height: 100vh;
    box-sizing: border-box;
}

.header-text{
    display: flex;
    flex-direction: column;
    text-align: center;
}

.header-text > h1{
    font-family: Poppins;
    font-size: 3em;
    font-weight: 800;
    font-style: normal;
    line-height: 62px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #fff;
    margin: 10px;
}

.header-text > p{
    font-family: Poppins;
    font-size: 20px;
    font-weight: 700;
    font-style: normal;
    line-height: 20px;
    text-align: center;
    /* color: #e5e5e5; */
    color: #fff;
    margin: 10px;
}

.head-btns{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin: 35px 10px;
}

.btn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0px 36px;
    height: 52px;
    left: 0px;
    right: 0px;
    text-decoration: none;
    box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
    border-radius: 40px;
    margin: 4px;
}

.btn-white{
    background: #fff;
    color: #112d57;
}

.btn-transparent{
    border: 1px solid #fff;
    box-sizing: border-box;
    color: #fff;
}

.btn:hover{
    box-shadow: 1px 10px 32px rgba(0, 0, 0, 0.45);
    transition: all 0.3s ease-in-out;
}

.btn-text{
    font-family: Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.2px;
    text-align: center;
}

.splash-image{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Home-anime{
    margin-bottom: -80px;
    border-radius: 50%;
    height: 500px;
    width: 100%;
}

/* For small height screen sizes */
@media only screen and (max-height: 550px) {
    .Home-anime {
        width: 400px;
        height: auto;
    }
}

/* For small width screens */
@media only screen and (max-width: 640px) {
    .Home-anime {
        /* width: 85%; */
        height: auto;
        align-self: center;
        margin-bottom: -20px;
    }

    .head-btns {
        margin: 10px 10px 35px 10px;
    }
}